



























import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import wx from 'weixin-js-sdk'
import { formatDate } from '@/until/index'

@Component({
  components: {
    HelloWorld
  },
  filters: {
    getFormatDate (date: any) {
      return new Date(date).getFullYear() + '' + (new Date(date).getMonth() + 1) + '' + new Date(date).getDate()
    },
    fenToYuan (num: any) {
      if (typeof num !== 'number' || isNaN(num)) return null
      return '' + (num / 100).toFixed(2)
    }
  }
})
export default class Home extends Vue {
  $createDialog : any
  $createToast : any
  // 
  model = { phone: '', openid: '' }
  fields =[{ type: 'input', modelKey: 'phone', label: '', props: { placeholder: '' }, rules: { required: true, type: 'tel' } }, { type: 'input', modelKey: 'verificationCode', label: '', props: { placeholder: '', type: 'text' }, rules: { required: true } }]
  valid = undefined
  validity = {}
  options = {}
  time = 60 // 
  sendMsgDisabled = false
  isVerification = false // 
  openid = ''
  jump = '' // 

  actions = [
    {
      text: ' 00.00',
      action: 'moreMoney'
    },
    {
      type: 'button',
      text: '<div class="payButton"></div>',
      action: 'payButtonClick'
    }
  ]
  selectCost = 0 //   
  totalCost = 0 //   
  checkList: any = []
  billList = []
  houseId = ''
  // tab
  selectedLabelDefault = 'incomplete'
  tabBarList = [{
    label: '',
    value: 'incomplete'
  }, {
    label: '',
    value: 'complete'
  }]

  // 
  page = 1
  isShowMore = true
  mounted () {
    this.getWeiXinConfig()
    this.pageInit()
  }

  activated () {
    this.getWeiXinConfig()
    this.pageInit()
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 
  get computedMsg () {
    return 'computed ' + this.propB
  }
  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log(result)
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  getBindingInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '...'
      })
      toast.show()
      this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          } else if (response.data.sessionId) {
            this.$store.commit('SET_SESSION_ID', response.data.sessionId)
            this.$store.commit('SET_USER_INFO', response.data.userInfo)
            this.getBillListByHouseId()
          }
        }
        toast.hide()
      })
    } else {
      console.log('')
    }
  }
  pageInit () {
    let query = this.$route.query
    if (query.houseId) {
      this.houseId = query.houseId
    } else {
      this['$createDialog']({
        type: 'alert',
        content: '',
        icon: 'cubeic-wrong'
      }).show()
      return
    }
    if (this.$store.state.sessionId) {
      this.getBillListByHouseId()
    } else if (query.code) {
      this.getBindingInfo()
    }
  }
  getBillListByHouseId () {
    const toast = this['$createToast']({
      time: 0,
      txt: '...'
    })
    toast.show()
    this.$store.dispatch('getBillListByHouseId', { houseId: this.houseId, type: this.selectedLabelDefault, page: this.page, limit: 1 }).then((response) => {
      if (response.success) {
        if (this.selectedLabelDefault === 'incomplete') {
          this.billList = response.data.list
          this.initCheckbox()
        } else {
          if (response.data.list.length > 0) {
            this.billList = this.billList.concat(response.data.list)
          } else {
            this.isShowMore = false
          }
        }
      }
      toast.hide()
    })
  }
  initCheckbox () {
    let arr:any = []
    let totalCost = 0
    this.billList.forEach((item:any) => {
      arr.push(item.id)
      totalCost += item.cost
    })
    this.totalCost = totalCost
    this.selectCost = totalCost
    this.actions[0].text = this.fenToYuan(totalCost)
    this.checkList = arr
  }
  computedPrice (list: any) {
    let totalCost = 0
    this.billList.forEach((item) => {
      list.forEach((id:any) => {
        if (item.id === id) {
          totalCost += item.cost
        }
      })
    })
    this.selectCost = totalCost
    this.actions[0].text = this.fenToYuan(totalCost)
  }
  checkboxChange (list: any) {
    this.computedPrice(list)
  }
  clickHandler (item: any) {
    if (item.action) {
      this[item.action](item)
    }
  }
  fenToYuan (num: any) {
    if (typeof num !== 'number' || isNaN(num)) return null
    return ' ' + (num / 100).toFixed(2)
  }
  getWeiXinConfig () {
    // let url = window.location.origin + window.location.pathname
    let url = window.location.href
    this.$store.dispatch('getWeixinConfig', { url: url }).then((res)=>{
      console.log('', res)
      if (res.success) {
        let config = res.data
        wx.config({
          debug: false, // ,apialertpclogpc
          appId: config.appId, // 
          timestamp: config.timestamp, // 
          nonceStr: config.nonceStr, // 
          signature: config.signature, // 
          jsApiList: ['chooseWXPay'] // JS
        })
      }
      wx.ready(function() {
        console.log('ready========')
      })
      wx.error(function(err:any) {
        console.log(err)
        console.log('errrrrrrrrrrrrrr')
      })
    })
  }

  payButtonClick () {
    let that = this
    let query = {}
    query.rqBillList = this.checkList
    query.fee = this.selectCost
    query.body = ''
    this.$store.dispatch('setUppropertyOrder', query).then((response) => {
      if (response.success) {
        let payData = response.data
        wx.chooseWXPay({
          timestamp: payData.timeStamp, // jssdktimestamptimeStampS
          nonceStr: payData.nonceStr, //  32 
          package: payData.package, // prepay_idprepay_id=\*\*\*
          signType: payData.signType, // 'SHA1''MD5'
          paySign: payData.paySign, // 
          success: function (res) {
            // 
            const toast = that.$createToast({
              time: 2000,
              txt: '...',
              mask: true,
              onTimeout: () => {
                that.getBillListByHouseId()
                that['$createDialog']({
                  type: 'alert',
                  content: '',
                  icon: 'cubeic-ok'
                }).show()
              }
            })
            toast.show()
          },
          error: function () {

          }
        })
      }
    })
  }

  tabBarChange (value:any) {
    this.page = 1
    this.isShowMore = true
    this.billList = []
    this.getBillListByHouseId()
  }
  showMoreClick () {
    this.page++
    this.getBillListByHouseId()
  }
}
